.home{
  background-color: #212121;
  min-height: 100vh
}
.shangpin{
  display: -ms-flexbox;
  display: flex;
  width: 900px;
  margin: 0 auto;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.shape{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  margin-top: 50px;
}
.shape div{
  width: 250px;
  height: 200px;
  background-size: 280px;
  background-position: center center;
}
.shape span{
  margin-top: 25px;
  width: 250px;
  line-height: 35px;
  border-radius: 2px;
  background: -webkit-gradient(linear, left top, right bottom, from(#ff9e80) , to(#f50057)
 );
  background: -webkit-linear-gradient(top left, #ff9e80 , #f50057
 );
  background: -o-linear-gradient(top left, #ff9e80 , #f50057
 );
  background: linear-gradient(to bottom right, #ff9e80 , #f50057
 );
  color: #fff;
  cursor: pointer;
}
.home .shape .yigou{
  /*background-color: #37474f;*/
  background: -webkit-gradient(linear, left top, right bottom, from(#9e9e9e) , to(#9e9e9e));
  background: -webkit-linear-gradient(top left, #9e9e9e , #9e9e9e);
  background: -o-linear-gradient(top left, #9e9e9e , #9e9e9e);
  background: linear-gradient(to bottom right, #9e9e9e , #9e9e9e);
}



